:root {
  --primaryColor: #90A959;
  --secondColor: #E9B872;
  --mainWhite: #fff;
  --mainBlack: #222;
  --mainRed: #c45151;
  --mainGreen: #4caf50;
}

*{
  margin: 0;
  box-sizing: border-box;
}

body{
  background: var(--secondColor);
  color: var(--mainBlack);
}

.main-container{
  margin: 0 auto;
  max-width:1280px;
  width: 90%;
}